




export default {
	props: {
		type: {
			type: String,
			default: 'default'
		},
		inverted: {
			type: Boolean,
			default: false
		},
		text: {
			type: String,
			default: ''
		}
	},
	computed: {
		setClass() {
			let classList = ['uni-badge-' + this.type];
			if (this.inverted === true) {
				classList.push('uni-badge-inverted')
			}
			return classList.join(" ")
		}
	},
	methods: {
		onClick() {
			this.$emit('click')
		}
	}
}



